import React from 'react'
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
// import MoreVertIcon from '@material-ui/icons/MoreVert';
import Chip from '@material-ui/core/Chip';
import Link from '@material-ui/core/Link';


// 
// For styles
//
const styles = theme => ({
  card: {
    display: "inline-block",
    float: "left",
    width: "35vw",
    maxHeight: "80vh",
    marginLeft: "5%",
    marginBottom: "2%",
    paddingBottom: "1%",
    background: "#fafcfe",
  },
  root: {
    paddingBottom: "1%",
  },
  cardHeader: {
    padding: "2%",
  },
  cardContent: {
    display: "inline-block",
    padding: "1%  1%",
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  actions: {
    display: 'flex',
    padding: '0px',
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  chip: {
    // margin: theme.spacing.unit,
    margin: "0.2em",
    fontSize: "0.75em",
    height: "1.6em",
    variant: "outlined",
  },
  link: {
    margin: theme.spacing.unit,
    color: 'inherit',
    width: "5%", 
    height: "5%", 
    marginLeft: "5%",
  },
  button: {
    margin: theme.spacing.unit,
    padding: "0% 2%",
    fullWith: true,
    size: "small",
    mini: true,
  },
});

function TechnologyChips(props) {
  const { classes } = props;

  return (
    props.project.technologies.map(tech => (
      <Chip label={tech} className={classes.chip} variant="outlined" />
    ))
  );
}

function GitHub(props) {
  return (
    <Link 
      target="_blank" rel="noopener noreferrer"
      href={props.project.github}
      className={props.classes.link}
    >
     <svg className="j2dfb39" focusable="false" viewBox="0 0 24 24" aria-hidden="true" role="presentation"><path d="M12 .3a12 12 0 0 0-3.8 23.4c.6.1.8-.3.8-.6v-2c-3.3.7-4-1.6-4-1.6-.6-1.4-1.4-1.8-1.4-1.8-1-.7.1-.7.1-.7 1.2 0 1.9 1.2 1.9 1.2 1 1.8 2.8 1.3 3.5 1 0-.8.4-1.3.7-1.6-2.7-.3-5.5-1.3-5.5-6 0-1.2.5-2.3 1.3-3.1-.2-.4-.6-1.6 0-3.2 0 0 1-.3 3.4 1.2a11.5 11.5 0 0 1 6 0c2.3-1.5 3.3-1.2 3.3-1.2.6 1.6.2 2.8 0 3.2.9.8 1.3 1.9 1.3 3.2 0 4.6-2.8 5.6-5.5 5.9.5.4.9 1 .9 2.2v3.3c0 .3.1.7.8.6A12 12 0 0 0 12 .3"></path></svg>
    </Link>
  );
}

function DeployedApp(props) {
  if (props.project.deployedAt.length === 0) {
    return;
  } else {
    return (
      <Button
        target="_blank" rel="noopener noreferrer"
        href={props.project.deployedAt}
        variant="contained" 
        className={props.classes.button}>
        Live App
      </Button>
    );
  }
}

function PortfolioCard(props) {
  const { classes } = props;
  
  return (
    <div className="portfolio-card">
      <Card className={classes.card}>
        <CardHeader className={classes.cardHeader}
          // action={
          //   <IconButton>
          //     <MoreVertIcon />
          //   </IconButton>
          // }
          title={props.project.name}
          // subheader={props.project.description}
        />
        <CardMedia
          className={classes.media}
          image={props.project.image}
          title={props.project.title}
        />

        <CardContent className={classes.cardContent} style={{paddingBottom: "1%"}}>
          {TechnologyChips(props)}
        </CardContent>

        <CardActions className={classes.actions} disableActionSpacing>
          {GitHub(props)}
          {DeployedApp(props)}
          <IconButton
            className={classnames (classes.expand, {
              [classes.expandOpen]: props.project.expanded,
            })}
            onClick={props.onClick}
            aria-expanded={props.project.expanded}
            aria-label="Show more"
          >
          <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={props.project.expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph style={{ fontWeight: "bold" }}>
              {props.project.title}
            </Typography>
            <Typography paragraph>
              {props.project.description}
            </Typography>
          </CardContent>
        </Collapse>

      </Card>

    </div>
  );
}

PortfolioCard.propTypes = {
  classes: PropTypes.object.isRequired,
  project: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
}

export default withStyles(styles)(PortfolioCard);
