import React from 'react';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import MenuIcon from '@material-ui/icons/Menu';
import { blue } from '@material-ui/core/colors';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  left: {
    alignItems: "flex-start",
    flexGrow: 11,
  },
  bar: {
    backgroundColor: blue[300],
  },
  menuButton: {
    // marginLeft: -18,
    // marginRight: 10,
    alignContent: "flex-end",
  },
  button: {
    margin: theme.spacing.unit,
  },
});

function currentLocation(props) {
  const pathname = props.location.pathname;

  if (pathname === '/') {
    return "about";
  } else if (pathname === '/portfolio') {
    return "portfolio";
  }

  return "about";
}

//
// Update page sub-title in the nav-bar per location
//
function pageSubtitle(props) {
  let subtitle = "";
  const location = currentLocation(props);

  if (location === 'about') {
    subtitle = "About Me";
  } else if (location === 'portfolio') {
    subtitle = "portfolio"
  }

  return subtitle;
}

//
// Update page menu in the nav-bar per location
//
function pageMenu(props) {
  let subtitle = "";
  const location = currentLocation(props);

  if (location === 'about') {
    subtitle = "Portfolio";
  } else if (location === 'portfolio') {
    subtitle = "About"
  }

  return subtitle;
}

function menuButton(props) {
  const { classes } = props;
  const location = currentLocation(props);

  if (location === "portfolio") {
    return (
      <IconButton className={classes.menuButton} color="inherit" aria-label="Menu">
        <MenuIcon />
      </IconButton>
    );  
  } else {
    return;
  }
}

function DenseAppBar(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <AppBar position="static" className={classes.bar}>
        <Toolbar variant="regular">
          <Typography variant="h6" color="inherit" className={classes.grow}>
            Motohiko Makino
          </Typography>
          {menuButton(props)}
          <Typography variant="subtitle1" color="inherit" className={classes.left}>
            {pageSubtitle(props)}
          </Typography>
          <Typography variant="subheading" color="inherit">
            <Button 
              variant="outlined" 
              color="inherit" 
              href={"/#/" + pageMenu(props).toLowerCase()} 
              className={classes.button}>
              {pageMenu(props)}
            </Button>
          </Typography>
        </Toolbar>
      </AppBar>
    </div>
  );
}

DenseAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
};

export default withStyles(styles)(withRouter(DenseAppBar));
