
import React from 'react'
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { withStyles } from '@material-ui/core/styles';

// 
// For styles
//
const styles = theme => ({
  root: {
    marginLeft: "auto",
    marginRight: "auto",
    marginBottom: "7%",
    width: "40%",
    display: "block",
    testAlign: "center",
  },
  link: {
    margin: theme.spacing.unit,
    color: '#555',
    fontSize: "1.2em",
    textAlign: "center",
  },
});

function Footer(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Typography className="links">
        <Link
          className={classes.link}
          target="_blank" 
          rel="noopener noreferrer" 
          href="https://www.linkedin.com/in/makino" >
          <i className="fa fa-linkedin-square fa-1g" aria-hidden="true"></i>
          LinkedIn
        </Link> 
        <Link 
          className={classes.link}
          target="_blank" 
          rel="noopener noreferrer" 
          href="https://github.com/mmakino">
          <i className="fa fa-github fa-1g" aria-hidden="true"></i>
          GitHub
        </Link> 
        <Link 
          className={classes.link}
          href="mailto:motohiko.makino@gmail.com?Subject=Reaching out via GitHub" 
          target="_top">
          <i className="fa fa-envelope-o" aria-hidden="true"></i>
          Email
        </Link>
      </Typography>
    </div>
  )
}
export default withStyles(styles)(Footer);
