import React, { Component } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import AppBar from './AppBar';
import AboutMe from './AboutMe';
import Portfolio from './Portfolio';
import projects from './projects';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      projects: this.initProjects(),
    };
    this.handleCardExpandClick = this.handleCardExpandClick.bind(this);
  }

  initProjects() {
    return projects.slice().map(prj => {
      prj.expanded = false;
      return prj;
    });
  }

  handleCardExpandClick(i) {
    const prj = this.state.projects.slice();
    
    prj[i].expanded = !this.state.projects[i].expanded;
    this.setState({ projects: prj });
  }

  render() {
    return (
      <Router>
        <nav>
          <AppBar />
        </nav>

        <section>
          <Switch>
            <Route
              exact path="/portfolio"
              render={props => 
                <Portfolio
                  projects={this.state.projects}
                  handleCardExpandClick={i => this.handleCardExpandClick(i)}
              />}
            />
            <Route
              path="/"
              render={props => 
                <AboutMe
                  projects={this.state.projects}
                  {...props}
                />}
            />
          </Switch>
        </section>
      </Router>
    )
  }
}

export default Main;
